.books {
  width: 100%;
  margin-bottom: 5rem;
}
.filters {
  margin-left: 18rem;
  // background-color: #f3f3f3;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}
.corner-image {
  position: absolute;
  height: 20rem;
  width: 20rem;
  margin: 3rem;
}
.books-top {
  height: 100%;
  padding: 2rem;
  margin: 2rem;
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.books-searches {
  height: 100%;
  margin: 2rem;
  padding: 2rem;
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.select-genre {
  margin-top: 2rem;
  margin-bottom: 2rem;
  width: 30rem;
  height: 4rem;
  font-size: 2rem;
  border-radius: 0.4rem;
  outline: none;
}
.input-search {
  margin-top: 2rem;
  margin-bottom: 2rem;
  width: 30rem;
  height: 4rem;
  font-size: 2rem;
  border-radius: 0.4rem;
  outline: none;
  border: solid 1px;
}
.input-author {
  margin-top: 2rem;
  margin-bottom: 2rem;
  width: 30rem;
  height: 4rem;
  font-size: 2rem;
  border-radius: 0.4rem;
  outline: none;
  border: solid 1px;
}

.posts-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  min-height: 25vh;
}

.paginate {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 5rem;
}
.pagi-button {
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.25),
    0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  background-color: rgb(255, 255, 255);
  height: 3rem;
  width: 3rem;
  margin: 0.7rem;
  margin-bottom: 5rem;
  color: rgb(129, 86, 214);
  font-size: 2.5rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: rgb(210, 192, 247);
  }
  &:active {
    transform: translateY(-0.2rem);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
}
.pagi-button-selected {
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  background-color: rgb(129, 86, 214);
  height: 3rem;
  width: 3rem;
  margin: 0.7rem;

  color: white;
  font-size: 2.5rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: rgb(89, 47, 173);
  }
  &:active {
    transform: translateY(-0.2rem);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
}
.no-match {
  text-align: center;
  font-size: 5rem;
}
@media (max-width: 1024px) {
  .books {
    grid-column: page-start/page-end;
  }
  .filters {
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
}
@media (max-width: 600px) {
  .books {
    grid-column: page-start/page-end;
  }
  .filters {
    margin-left: 6rem;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  .input-search {
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding: 1rem;
    width: 25rem;
    height: 4rem;
    font-size: 2rem;
    border-radius: 0.4rem;
  }
  .input-author {
    margin-top: 1rem;
    margin-bottom: 1rem;
    width: 25rem;
    height: 4rem;
    font-size: 2rem;
    padding: 1rem;
  }
  .books-searches {
    margin: 0.5rem;
    padding: 0.5rem;
  }
  .books-top {
    margin: 0.5rem;
    padding: 0.5rem;
  }

  .select-genre {
    margin-top: 1rem;
    margin-bottom: 1rem;
    width: 25rem;
    height: 4rem;
    font-size: 2rem;
  }

  .corner-image {
    position: absolute;
    height: 15rem;
    width: 15rem;
    margin: 2rem;
  }
}
// animations
.animated {
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.animated-delay {
  -webkit-animation-duration: 3s;
  animation-duration: 3s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  -webkit-animation-name: fade-in;
  animation-name: fade-in;
}
