.card {
  margin-top: 7rem;
  margin-bottom: 7rem;
  margin-left: 2rem;
  margin-right: 2rem;
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 22rem;
  height: 28rem;
  padding: 2rem 2rem;
  border-radius: 0.5rem;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 1rem 1rem rgba(0, 0, 0, 0.22);
  transition: all 0.5s; //cubic-bezier(.25,.8,.25,1)
  background-size: 100%;
  opacity: 0.9;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
  // test

  .cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    align-items: center;
    justify-content: center;
    transition: all 0.5s;
    overflow: hidden;
    padding: 2.5rem;
    text-align: center;
  }

  &:hover {
    background-size: 150%;
    box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.25),
      0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
    .cover {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: column;
      // background-color: rgba(129, 86, 214, 0.966);
      background-color: rgba(84, 30, 192, 0.966);
      color: white;
      font-size: 2rem;
      z-index: 2;
    }
    .price {
      bottom: -10rem;
    }
    .title {
      transform: translateY(-120%);
    }
  }
  .price {
    opacity: 1;
    position: absolute;
    bottom: 0;
    right: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 10px;
    // background-color: rgba(129, 86, 214, 0.945);
    background-color: rgba(102, 39, 228, 0.945);
    font-size: 1.8rem;
    color: white;
    transition: all 0.5s ease-in;
    border-radius: 0.2rem;
    text-shadow: 2px 2px rgba(black, 0.7);
  }
  .title {
    opacity: 1;
    color: white;
    transition: all 0.5s ease-in;
    z-index: 1;
    margin: 0 0 5px;
    text-align: center;
    font-size: 2.5rem;

    //line-height: 1.35;
    text-shadow: 2px 2px rgba(black, 0.7);
    span {
      background-color: rgb(95, 32, 221);
      //  background-color: rgb(129, 86, 214);
    }
  }
}
.card-button {
  border: white 0.4rem solid;
  padding: 0.3rem;
  cursor: pointer;
  color: rgb(129, 86, 214);
  height: 3.5rem;
  font-size: 2rem;
  border-radius: 0.4rem;
  outline: none;
  &:active {
    transform: translateY(-0.2rem);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
}
.cover-title {
  font-size: 2.5rem;
  text-shadow: rgba(0, 0, 0, 0.22);
}
.hor-line {
  width: 90%;
}
