.cart {
  background-color: rgb(243, 243, 243);
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  grid-column: content-start/content-end;
  width: 100%;
  min-height: 100vh;
  justify-content: center;
}
.cart-image {
  height: 20rem;
  width: 20rem;
  margin: 3rem;
}
.cart-text {
  top: 50%;
  font-size: 9rem;
  margin: auto;
  background-color: rgba(129, 86, 214, 0.76);
  color: white;
}
.cart-header {
  padding: 1rem;
  height: min-content;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom: 1rem;
}
.cart-empty {
  width: 100%;
  margin-top: 3rem;
  text-align: center;
}
.cart-button-container {
  width: 100%;
  position: relative;
  align-self: flex-end;
  margin: 2rem;
  margin-top: 8rem;
  margin-bottom: 15rem;
  height: min-content;
  width: min-content;
  &:hover {
    .cart-button-errors {
      visibility: visible;
    }
  }
}

.cart-button-errors {
  visibility: hidden;
}
.cart-button-error {
  font-size: 1rem;
  color: red;
  text-align: right;
}
.cart-button {
  position: relative;
  margin: 2rem;
  border: none;
  outline: none;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  background-color: #8156d6;
  color: whitesmoke;
  border-radius: 0.4rem;
  width: 25rem;
  height: 5rem;
  font-size: 2rem;
  &:hover {
    cursor: pointer;
    background-color: rgb(89, 47, 173);
  }
  &:active {
    transform: translateY(-0.1rem);
    //transform: translateX(-50%);
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
  }
  &:disabled {
    background-color: rgb(177, 174, 174);
    cursor: no-drop;
  }
}
.cart-total {
  text-align: right;
  font-size: 3rem;
}
@media (max-width: 1024px) {
  .cart {
    grid-column: page-start/page-end;
  }
}
// animations
.animated {
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.animated-delay {
  -webkit-animation-duration: 3s;
  animation-duration: 3s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  -webkit-animation-name: fade-in;
  animation-name: fade-in;
}
