.header {
  grid-column: page-start/page-end;

  width: 100%;
  height: 120vh;
  min-height: 65rem;
}
.header-image {
  width: 100%;
  min-height: 65rem;
}
.quote {
  position: absolute;
  top: 20%;
  left: 40%;
  width: 40%;
  height: 70%;
  min-height: 45rem;
  padding: 2%;
  color: white;
  mix-blend-mode: multiply;
  background-color: rgba(129, 86, 214, 0.76);
}
.quote-text {
  text-align: center;
  font-size: 3.5rem;
}
.quote-button {
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  font-size: 2.3rem;
  cursor: pointer;
  text-decoration: none;
  color: white;
}

@media (max-width: 1024px) {
  .header {
    grid-column: page-start/page-end;
    height: 55vh;
  }
  .header-image {
    height: 55vh;
  }
  .quote {
    position: absolute;
    top: 5%;
    left: 40%;
    width: 50%;
    height: 40%;
    background-color: rgb(129, 86, 214);
  }
  .quote-text {
    text-align: center;
    font-size: 2rem;
  }
}
@media (max-width: 600px) {
  .header {
    grid-column: page-start/page-end;
  }
  .quote-button {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
    font-size: 1.5rem;
    cursor: pointer;
    text-decoration: none;
    color: white;
  }
  .arrow {
    font-size: 1.2rem;
  }
}

// animations
.animated {
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.animated-delay {
  -webkit-animation-duration: 3s;
  animation-duration: 3s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}

@-webkit-keyframes fade-in-left {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-3rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

@keyframes fade-in-left {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.fade-in-left {
  -webkit-animation-name: fade-in-left;
  animation-name: fade-in-left;
}

@keyframes arrow-animation {
  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(0.3rem, 0);
  }
}
.arrow {
  animation: arrow-animation 1s ease-in-out infinite;

  font-size: 1.7rem;
}
